<template>
  <div class="pay">
    <div class="payWarp">
      <div class="labelnum">选择充值金额</div>
      <ul class="nums">
        <li
          v-for="(item,i) in nums"
          :class="money==item&&!Other?'active':''"
          @click="handeMoney(item)"
          :key="i"
        >{{item}}元</li>
      </ul>
    </div>
    <div class="Other" @click="changeOther">
      其他金额充值
      <van-icon v-show="!Other" name="arrow-down"/>
      <van-icon v-show="Other" name="arrow-up"/>
    </div>
    <div class="numWarp" :style="!Other?'height:0;overflow: hidden;':''">
      <div class="numInput" @click="show = true">
        <strong>¥</strong>
        <span>{{value}}</span>
      </div>
      <div class="errinfo" :style="moneyErr?'color:red;':''">请输入金额≥300的整数</div>
    </div>
    <div class="payType">
      <div class="typeTitle">选择充值方式</div>
      <van-radio-group v-model="radio">
        微信支付
        <van-radio class="payradio" name="1"></van-radio>
      </van-radio-group>
    </div>
    <!-- 我在店里 -->
    <div class="instore">
      {{storeName.name}}
      <van-icon name="arrow" @click="choosingStore"/>
    </div>
    <div class="concent">
      <div class="box">
        <!-- /*注释4*/ -->
        <div
          v-for="(item,i) in techList"
          @click="handeTech(item.id)"
          :class="activeTech==item.id?'active':''"
          :key="item.id"
        >
          <div class="images">
            <img :src="item.img" alt>
          </div>
          <div class="name">{{item.name}}</div>
        </div>
      </div>
    </div>

    <van-button @click="handeply" :disabled="moneyErr&&!money" class="playbtn">确认充值</van-button>
    <!-- 数字键盘 -->
    <van-number-keyboard
      :show="show"
      @input="onInput"
      @delete="onDelete"
      @blur="show = false"
      theme="custom"
      close-button-text="完成"
    />
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
import {
  Icon,
  Row,
  Cell,
  CellGroup,
  Swipe,
  Toast,
  SwipeItem,
  NumberKeyboard,
  PasswordInput,
  Button,
  RadioGroup,
  Radio
} from "vant";
// Vue.use(PasswordInput).use(NumberKeyboard);
export default {
  components: {
    [Icon.name]: Icon,
    [Row.name]: Row,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Swipe.name]: Swipe,
    [Toast.name]: Toast,
    [SwipeItem.name]: SwipeItem,
    [NumberKeyboard.name]: NumberKeyboard,
    [PasswordInput.name]: PasswordInput,
    [Button.name]: Button,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio
  },

  data() {
    return {
      show: false,
      Other: false, //是否其他金额充值
      value: "",
      showKeyboard: true,
      nums: [300, 500, 800, 1000], //充值金额
      money: "", //当前金额
      radio: "1",
      moneyErr: true,
      storeName: {
        name: "我在店里",
        id: ""
      },
      techList: [
        // {
        //   name: "小李",
        //   id: 1,
        //   img:
        //     "http://images.yyhealth.com/technician/201903/2019032815132927275c9c741968a1f.jpeg"
        // },
        // {
        //   name: "小王",
        //   id: 2,
        //   img:
        //     "http://images.yyhealth.com/technician/201903/2019032815132927275c9c741968a1f.jpeg"
        // },
        // {
        //   name: "小刘",
        //   id: 3,
        //   img:
        //     "http://images.yyhealth.com/technician/201903/2019032815132927275c9c741968a1f.jpeg"
        // },
        // {
        //   name: "小赵",
        //   id: 4,
        //   img:
        //     "http://images.yyhealth.com/technician/201903/2019032815132927275c9c741968a1f.jpeg"
        // },
        // {
        //   name: "小宋",
        //   id: 5,
        //   img:
        //     "http://images.yyhealth.com/technician/201903/2019032815132927275c9c741968a1f.jpeg"
        // },
        // {
        //   name: "小东",
        //   id: 6,
        //   img:
        //     "http://images.yyhealth.com/technician/201903/2019032815132927275c9c741968a1f.jpeg"
        // }
      ],
      activeTech: ""
    };
  },
  mounted() {
    // this.$http.get("/memberLs/getUserInfo", function(res) {
    //   console.log(res);
    // }); // location.href="http://yycms.test.yyhealth.com/admin/memberLs/getUserInfo"
  },
  created() {
    //选择店铺判断
    if (this.currentStore.id) {
      console.log("存在选择状态的店铺");
      this.storeName = { ...this.currentStore };
    } else {
      console.log("未选择");
    }
  },
  computed: {
    ...mapState({
      currentCity: "city",
      currentStore: "store"
    })
  },
  methods: {
    Fixed2(num) {
      //两位小数判断
      var blo = /(^[1-9]+$)|(^-?\d+\.?\d{0,2}$)/.test(num);
      return blo;
    },
    handeTech(id) {
      //技师点击
      console.log(id);
      this.activeTech = id;
    },
    changeOther() {
      //更改数额类型
      this.Other = !this.Other;
      this.show = this.Other;
      this.moneyErr = true;
      this.money = "";
      this.value = "";
    },
    handeMoney(num) {
      //充值金额点击
      this.money = num;
      this.Other = false;
      this.value = "";
    },
    onInput(key) {
      //键盘输入
      var itemVal = this.value + key;
      if (itemVal < 300) {
        this.moneyErr = true;
      } else {
        this.moneyErr = false;
      }
      if (!this.Fixed2(itemVal) || itemVal.length > 8) {
        return;
      }
      this.value = this.value + key;
      Toast(this.value);
      window.sessionStorage.setItem("values", this.value);
      console.log(this.value.length);
    },
    onDelete() {
      //删除
      this.value = this.value.slice(0, this.value.length - 1);
      var itemVal = this.value;
      if (itemVal < 300) {
        this.moneyErr = true;
      } else {
        this.moneyErr = false;
      }
    },
    choosingStore() {
      //选择门店
      var moneyData = {
        Other: this.Other,
        value: this.value,
        money: this.money
      };
      //保存数据
      window.sessionStorage.setItem("moneyData", moneyData);
      //选择门店
      this.$router.push("/choosingStore");
    },
    handeply() {
      //充值金额
      var moneyData = {
        Other: this.Other,
        value: this.value,
        money: this.money
      };
      var money = "";
      if (this.Other) {
        money = this.value;
        if (money < 300) {
          this.moneyErr = true;
          return;
        } else {
          this.moneyErr = false;
        }
      } else {
        money = this.money;
      }
      if (!money) {
        return Toast("充值金额不可为空...");
      }
      Toast(money + "支付");
    }
  }
};
</script>

<style scope lang="less">
