body {
  background: #f7f7f7 !important;
  min-height: 100%;
}
.pay {
  .payWarp {
    background: url(/card/images/pay_background@2x.png);
    background-size: 100%;
    height: 2.54rem;
    width: 7.5rem;
    padding: 0.32rem;
    box-sizing: border-box;
    .labelnum {
      height: 0.28rem;
      line-height: 0.28rem;
      font-size: 0.28rem;
      font-family: PingFangSC-Semibold;
      font-weight: 600;
      color: rgba(107, 107, 107, 1);
    }
  }
  .Other {
    height: 1.09rem;
    line-height: 1.06rem;
    background: #ffffff;
    font-size: 0.4rem;
    padding: 0 0.3rem;
    font-size: 0.28rem;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(44, 44, 44, 1);
    .van-icon {
      transform: translateY(0.02rem);
      font-weight: 800;
      font-size: 0.25rem;
      color: #c1c1c1;
    }
  }
  .numWarp {
    height: 1.48rem;
    padding: 0 0.32rem;
    box-sizing: border-box;
    background: #fff;
    margin-bottom: 0.16rem;
    overflow: hidden;
    background: #fff;
    .numInput {
      overflow: hidden;
      height: 0.48rem;
      line-height: 0.48rem;
      font-size: 0.4rem !important;
      strong {
        font-size: 0.48rem;
        height: 0.48rem;
        width: 0.29rem;
        line-height: 0.48rem;
        font-family: PingFangSC-Semibold;
        font-weight: 600;
        color: rgba(44, 44, 44, 1);
        float: left;
      }
      span {
        float: left;
        height: 0.48rem;
        margin-left: 0.15rem;
        line-height: 0.48rem;
        &::after {
          content: "";
          height: 0.48rem;
          float: right;
          left: 0;
          width: 0.04rem;
          height: 0.48rem;
          background: rgba(1, 200, 1, 1);
          border-radius: 0.05rem;
        }
      }
    }
  }
  .nums {
    margin-top: 0.4rem;
    li {
      width: 1.53rem;
      box-sizing: border-box;
      height: 1.14rem;
      background: rgba(255, 255, 255, 1);
      border-radius: 0.08rem;
      text-align: center;
      line-height: 1.14rem;
      float: left;
      font-size: 0.32rem;
      margin-right: 0.24rem;
      font-family: MontserratAlternates-Bold;
      font-weight: bold;
      color: rgba(107, 107, 107, 1);
    }
    & li:last-child {
      margin: 0;
    }
    li.active {
      background: rgba(223, 101, 76, 1);
      color: rgba(255, 255, 255, 1);
    }
    li.refund {
      background: #ccc;
    }
  }
  .payradio {
    // padding-top: 0.1rem;
    float: right;
  }
  .van-radio-group {
    font-size: 0.4rem;
    line-height: 0.72rem;
    font-size: 0.32rem;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(44, 44, 44, 1);
    margin-top: 0.28rem;
    height: 0.72rem;
    .van-radio__icon--checked .van-icon {
      border-color: rgba(223, 101, 76, 1);
      background-color: rgba(223, 101, 76, 1);
    }
    // .van-icon::before {
    //   float: right;
    // }
  }
  .van-radio-group::after {
    float: left;
    content: "";
    height: 0.72rem;
    width: 0.64rem;
    background: url(/card/images/WeChat@2x.png) no-repeat left center;
    background-size: 0.44rem;
  }
  .instore {
    background: #fff;
    margin-top: 0.16rem;
    height: 0.4rem;
    line-height: 0.4rem;
    padding: 0.2rem 0.3rem;
    font-size: .28rem;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(44, 44, 44, 1);
    .van-icon {
      float: right;
      color: #C1C1C1;
      margin-top: .05rem;
    }
  }
  .playbtn {
    width: 6.86rem;
    margin: 0 auto;
    border: none;
    margin-left: 0.32rem;
    margin-top: 1.04rem;
    font-size: 0.36rem;
    font-family: PingFangSC-Semibold;
    font-weight: 600;
    color: rgba(255, 255, 255, 1);
    height: 0.88rem;
    line-height: 0.88rem;
    background: linear-gradient(
      90deg,
      rgba(224, 86, 63, 1) 0%,
      rgba(255, 121, 84, 1) 100%
    );
    box-shadow: 0px 10px 20px 0px rgba(253, 164, 146, 1);
    border-radius: 55px;
  }
  .errinfo {
    margin-top: 0.44rem;
    height: 0.24rem;
    line-height: 0.24rem;
    font-size: 0.3rem;
    font-size: 0.24rem;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(155, 155, 155, 1);
  }
}
.payType {
  width: 7.5rem;
  box-sizing: border-box;
  background: #fff;
  height: 1.88rem;
  padding: 0.32rem;
  .typeTitle {
    line-height: 0.28rem;
    font-size: 0.28rem;
    font-family: PingFangSC-Semibold;
    font-weight: 600;
    color: rgba(107, 107, 107, 1);
  }
}
.concent {
  width: 6.9rem;
  margin: 0 auto;
}
.box {
  white-space: nowrap;
  overflow-x: auto;
} /*注释1*/
.box::-webkit-scrollbar {
  width: 0;
  height: 0;
  display: none;
} /*注释2*/
.box > div {
  width: 1.8rem;
  height: 2.15rem;
  list-style: none;
  display: inline-block;
  line-height: 0.3rem;
  margin-right: 0.2rem;
  text-align: center;
  border: 0.01rem solid transparent;
  img {
    width: 1.5rem;
    border-radius: 50%;
    height: 1.5rem;
  }
} /*注释3*/
.box > div.active {
  border: 0.01rem solid #ccc;
}
.box .images {
  height: 1.5rem;
  margin-top: 0.15rem;
}
.box .name {
  text-align: center;
  font-size: 0.26rem;
  height: 0.5rem;
  line-height: 0.5rem;
}
.box div:last-child {
  margin: 0;
}
</style>
